@import "./style/_color";
@import "./style/_typography";
@import "./style/_responsive";

main {
    display: flex;
    min-width: 100vw;
    min-height: 100vh; }

.app-left {
    display: flex;
    flex: 1;
    width: 40vw;
    min-height: 100vh;
    overflow: scroll;
    -webkit-backdrop-filter: blur(50px);
    backdrop-filter: blur(50px); }

.app-right {
    position: fixed;
    top: 0;
    width: 100vw;
    height: 100vh;
    overflow: hidden;
    background-color: $theme-color;
    pointer-events: none; }

.app-content {
    z-index: 1;
    background-color: $grey-1; }

@include mobile {

    // 移动端
    body {
        background-color: $grey-9; }

    .app-left {
        width: 100vw;
        top: 0;
        left: 0;
        z-index: 1;
        background-color: $black-secondary; }

    .content {
        position: relative;
        padding: 48px 32px 96px 32px; }

    .app-content {
        width: 100vw;
        min-height: calc(100vh - 32px);
        overflow-y: scroll;
        position: fixed;
        top: 32px;
        right: 0;
        border-radius: 24px 24px 0 0;

        .close-button {
            position: fixed;
            right: 16px;
            top: 48px;
            z-index: 101; } }

    .app-content.hide {
        display: none;
        opacity: 0; } }


@include tablet {

    // 小屏电脑
    .app-left {
        background-color: rgba($grey-9,0.65);
        position: absolute;
        top: 0;
        left: 0;
        z-index: 999; }

    .content {
        padding: 64px; }

    .app-content {
        width: 60vw;
        max-height: 100vh;
        overflow: scroll;
        position: fixed;
        top: 0;
        right: 0;

        .close-button {
            position: fixed;
            right: 16px;
            top: 16px;
            z-index: 3; } } }

@include desktop {

    .app-left {
        background-color: rgba($grey-9,0.65);
        position: absolute;
        top: 0;
        left: 0;
        z-index: 999; }

    .content {
        padding: 96px; }

    .app-content {
        width: 60vw;
        min-height: 100vh;
        max-height: 100vh;
        overflow: scroll;
        position: fixed;
        top: 0;
        right: 0;

        .close-button {
            position: fixed;
            right: 32px;
            top: 32px;
            z-index: 101; } } }

@include desktop {

    .fade-enter {
        opacity: 0; }

    .fade-exit {
        opacity: 1; }

    .fade-enter-active {
        opacity: 1;
        transition: opacity .3s ease-in-out; }

    .fade-exit-active {
        opacity: 0;
        transition: opacity .3s ease-in-out; }

    .app-content-fade-enter {
        transform: translateX(-48px);
        opacity: 0; }

    .app-content-fade-exit {
        transform: translateX(0px);
        opacity: 1; }

    .app-content-fade-enter-active {
        transform: translateX(0px);
        opacity: 1;
        transition: all .6s ease-in-out; }

    .app-content-fade-exit-active {
        transform: translateX(-48px);
        opacity: 0;
        transition: all .6s ease-in-out; }

    .article-fade-enter {
        article {
            transform: translateX(-48px);
            opacity: 0; } }

    .article-fade-exit {
        article {
            transform: translateX(0px);
            opacity: 1; } }

    .article-fade-enter-active {
        article {
            transform: translateX(0px);
            opacity: 1;
            transition: all 0.6s ease-in-out; } }

    .article-fade-exit-active {
        article {
            transform: translateX(-48px);
            opacity: 0;
            transition: all 0.6s ease-in-out; } } }

@include tablet {

    .fade-enter {
        opacity: 0; }

    .fade-exit {
        opacity: 1; }

    .fade-enter-active {
        opacity: 1;
        transition: opacity .3s ease-in-out; }

    .fade-exit-active {
        opacity: 0;
        transition: opacity .3s ease-in-out; }

    .app-content-fade-enter {
        transform: translateX(-48px);
        opacity: 0; }

    .app-content-fade-exit {
        transform: translateX(0px);
        opacity: 1; }

    .app-content-fade-enter-active {
        transform: translateX(0px);
        opacity: 1;
        transition: all .6s ease-in-out; }

    .app-content-fade-exit-active {
        transform: translateX(-48px);
        opacity: 0;
        transition: all .6s ease-in-out; }

    .article-fade-enter {
        article {
            transform: translateX(-48px);
            opacity: 0; } }

    .article-fade-exit {
        article {
            transform: translateX(0px);
            opacity: 1; } }

    .article-fade-enter-active {
        article {
            transform: translateX(0px);
            opacity: 1;
            transition: all 0.6s ease-in-out; } }

    .article-fade-exit-active {
        article {
            transform: translateX(-48px);
            opacity: 0;
            transition: all 0.6s ease-in-out; } } }

@include mobile {

    .fade-enter {
        opacity: 0; }

    .fade-exit {
        opacity: 1; }

    .fade-enter-active {
        opacity: 1;
        transition: opacity .3s ease-in-out; }

    .fade-exit-active {
        opacity: 0;
        transition: opacity .3s ease-in-out; }

    .app-content-fade-enter {
        transform: translateY(300px);
        opacity: 0; }

    .app-content-fade-exit {
        transform: translateY(0px);
        opacity: 1; }

    .app-content-fade-enter-active {
        transform: translateY(0px);
        opacity: 1;
        transition: all .6s ease-in-out; }

    .app-content-fade-exit-active {
        transform: translateY(300px);
        opacity: 0;
        transition: all .6s ease-in-out; } }
