@import "color";
@import "typography";
@import "responsive";

.blog-article {
    // 文章内标签的全局样式
    width: 100%;
    min-height: 100vh;
    color: $black-main; // 文本颜色
    box-shadow: -1px 0 0 0 $white-divider;

    * {
        max-width: 100%; }

    a {
        color: $theme-color;
        position: relative;
        &:hover {
            opacity: 0.9; }
        &:before {
            content: '';
            position: absolute;
            left: 0;
            bottom: -2px;
            width: 0;
            height: 2px;
            -webkit-transition: all .6s ease;
            transition: all .6s ease;
            background-color: $theme-color; }
        &:hover:before {
            width: 100%;
            right: 0; } }

    p {
        color: $black-secondary;
        font-weight: 400;
        margin: 0; }

    img {
        box-shadow: 0px 16px 16px 0 $black-divider; }

    code {
        word-break: break-all; }

    blockquote {
        margin: 24px 0 0 0;
        padding: 8px 24px 24px 24px;
        background-color: $black-bg;
        border-left: 8px solid rgba($theme-color, 0.1); } }

.blog-article-container:first-child {
    margin-top: 0!important; }

@include mobile {

    .blog-article {
        position: absolute;
        z-index: 99; }

    .blog-article-container {
        width: auto;
        margin: 0 32px;
        padding: 48px 0 128px 0;

        h1 {
            @include text-32;
            padding-bottom: 8px; }

        h2 {
            @include text-24;
            margin-top: 48px;
            padding-bottom: 4px; }

        h3 {
            @include text-20;
            margin-top: 32px; }

        h4 {
            @include text-16;
            margin-top: 24px; }

        p {
            @include text-16;
            margin-top: 16px; }

        code {
            @include text-16; }

        img {
            margin-top: 16px; } } }

@include tablet {

    .blog-article-container {

        width: auto;
        margin: 0 64px;
        padding: 108px 0 192px 0;

        h1,h2,h3,h4,p {
            margin: auto auto;
            max-width: calc(100% - 64px); }

        h1 {
            @include text-48;
            padding-bottom: 24px; }

        h2 {
            @include text-40;
            margin-top: 64px;
            padding-bottom: 16px; }

        h3 {
            @include text-32;
            margin-top: 48px;
            padding-bottom: 8px; }

        h4 {
            @include text-24;
            margin-top: 32px; }

        p {
            @include text-20;
            margin-top: 24px; }

        code {
            @include text-20; }

        img {
            margin-top: 16px; } } }

@include desktop {

    .blog-article-container {

        width: auto;
        margin: 0 128px;
        padding: 160px 0 192px 0;

        h1 {
            @include text-48;
            padding-bottom: 24px; }

        h2 {
            @include text-40;
            margin-top: 64px;
            padding-bottom: 16px; }

        h3 {
            @include text-32;
            margin-top: 48px;
            padding-bottom: 8px; }

        h4 {
            @include text-24;
            margin-top: 32px; }

        p {
            @include text-20;
            margin-top: 24px; }

        code {
            @include text-20; }

        img {
            margin-top: 16px; } } }
