@import "typography";
@import "color";
@import "responsive";

a {
    font-weight: 600;
    text-decoration: none; }

a.icon-right {
    &:after {
        font-family: "Font Awesome 5 Free";
        content: "\f105";
        display: inline-flex;
        align-items: center;
        font-weight: 900;
        position: relative;
        left: 8px;
        transition: all .3s ease-in-out; }
    &:hover {
        &:after {
            transform: scale(1.1);
            left: 12px; } } }

a.button-link {
    &:after {
        display: none; } }

a.disabled {
    color: $white-disabled;
    pointer-events: none;
    &:hover {
        cursor: not-allowed; }
    &:after {
        display: none; } }

@include mobile {
    a.navlink {
        &:after {
            left: 8px; }
        &:hover::after {
            left: 16px; } } }

@include tablet {
    a.navlink {
        &:after {
            left: 8px; }
        &:hover::after {
            left: 16px; } } }

@include desktop {
    a.navlink {
        &:after {
            left: 16px; }
        &:hover::after {
            left: 24px; } } }
